@font-face {
    font-family: 'Foco';
    src: url(./fonts/AcerFoco.ttf)
}

@font-face {
    font-family: 'Foco Light';
    src: url(./fonts/AcerFoco_Rg.ttf);
    font-weight: 800;
}






.row {
    flex-wrap: nowrap !important;

    .col-md-auto,
    .col-lg-auto {
        -ms-flex: 0 0 auto;
        flex: 0 0 auto;
        width: auto;
        max-width: 100%;
    }

    .col-lg-3 {
        -ms-flex: 0 0 25%;
        flex: 0 0 25%;
        max-width: 25%;
    }
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    img {
        max-width: 100%;
    }

    #root {
        max-width: 630px;
        width: 630px;
        margin: auto;
        // background: linear-gradient(0deg, #0062cc, #0abcce);
        box-shadow: 5px 5px 150px rgba(0, 0, 0, .15);
        border-radius: 2rem;
        padding: 2rem;
        position: relative;
        height: 630px;
        font-family: "Foco";
        overflow: hidden;

        nav {
            flex-wrap: nowrap !important;
        }

        h2 {
            text-transform: uppercase;
            font-weight: 600;
        }

        .delo {
            color: #ff0099;
            font-family: monospace;
            font-size: 2.5rem;
            text-shadow: 2px 2px #fff;
        }

        .logo {
            img {
                height: 3rem;
                width: auto;
            }
        }
    }
}

body {
    width: 630px;
    height: 630px;
    margin: auto;
}